.enroll-content-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.enroll-maincontainer{
    /* border: 2px solid rgb(64, 255, 0); */
    padding: 4% 0 4% 0;
    margin-top: 5%;
    width: 70%;
    background-color: #fff;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
.enroll-maincontainer h1{
    padding-bottom: 1%;
    text-align: center;
}
.enroll-maincontainer p{
    letter-spacing: 2px;
    padding-bottom: 1%;
    text-align: center;
}
.enroll-content{
    /* border: 2px solid red; */
    display: flex;
    justify-content: space-evenly;
    
    padding: 1% 0% 1% 2%;
}
.enroll-courses{
    border: 2px solid black;
    height: fit-content;
    width: 400px;
}
.courses{
    padding: 16px 30px;
}
.courses:nth-child(even){
    background-color: rgb(234, 235, 236);
}
input{
    margin-right: 8px;
    cursor: pointer;
}


.cources-prices{
    /* border: 2px solid green; */
    padding: 1% ;
    width: 500px;
    margin-left: 5%;
}
.prices{
    background-color: aliceblue;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5% 5% 0 5%;
    margin-top: 10px;
    margin-bottom: 3%;
    width: 100%;
}
.price-month{
    display: flex;
    align-items: center;
}
.price-month p{
    font-weight: bold;
}

span{
    font-weight: 400;
    font-size: 12px;
    color: gray;
}
.price-month .btn{
    font-weight: 800;
    font-size: 12px;
    background-color: rgb(53, 53, 249);
    color: #fff;
    outline: none;
    border: none;
    padding: 8px;
    border-radius: 8px;
    margin-left: 20px;
    cursor: pointer;
}

.duration p{
    font-size: 10px;
    font-weight: 500;
    color: red;
    margin-top: 5px;
}
.duration h4{
    font-size: 20px;
}
#payment-time{
    color: gray;
    font-size: 12px;
    margin-top: 20px;
}

.questions-content{
    margin-top: 8%;
}
.questions{

    width: 70%;
    background-color: #f4f6f6;
    /* border: 2px solid red; */
    margin: 7% 0 5% 0;
    padding: 3% 4% 3% 4%;
}
.questions h1{
    text-align: center;
}




/* Bootstrap */
.accordion-item {
    background-color: #fff;
    border: 0px solid rgba(0,0,0,.125);
    margin-bottom: 9px;
}
.accordion-flush .accordion-item .accordion-button {
    border-radius: 0;
    font-weight: 600;
}
.accordion-button:not(.collapsed) {
    color: #000000;
    background-color: #e7f1ff;
    box-shadow: none;
}
.accordion-button:focus {
    border-color: #ffffff;
    box-shadow: none;
}
.border {
    border: 0px solid #dee2e6!important;
}
.accordion-button::after {
    background-image: url(../../../public/plus.svg);
}
.accordion-button:not(.collapsed)::after {
    background-image: url(../../../public/dash.svg);

}
.accordion-body {
    font-size: 12px;
}